<template>
  <div id="app">
    <div class="container">
      <header class="text-center">
        <h1 id="main-header">ToDoDo</h1>
        <h2>Simple to-do list</h2>
      </header>

      <div class="row mt-5">
        <div id='pad' class="col-md-6 offset-md-3 col-12 p-4">
            <div class="mb-2 p-3 d-flex justify-content-center">
              <input id="task-input" type="text" name="task" class="w-100 p-2" placeholder="Type task..." v-model="task.content" v-on:keyup.enter="addTask()">
          </div>
            <ul id="tasks" class="list-unstyled list-group-flush" v-for="(taskItem, n) in tasks">
                <li class="list-group-item d-flex justify-content-between task" v-bind:class="{ completed: taskItem.completed }">
                  <div style="word-break: break-all" v-bind:class="{ line: taskItem.completed }">{{taskItem.content}}</div>
                  <div class="justify-content-end">
                    <i class="fas fa-check complete my-auto mr-2" v-show="!taskItem.completed" @click="completeTask(n)"></i>
                    <i class="fas fa-minus-circle delete my-auto" @click="deleteTask(n)"></i>    
                  </div>
                </li>
            </ul>
        </div>
      </div>
    </div>
    <footer class="p-3 mt-4 text-center sticky-bottom">
      <h3>Created by Łukasz Dziedzic.</h3>
    </footer>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
        return {
         tasks: [
         ],
        task: {
          content: '',
          completed: 0,
        }
    }
  },
  mounted() {
    if (localStorage.getItem('tasks')) {
      try {
        this.tasks = JSON.parse(localStorage.getItem('tasks'));
      } catch(e) {
        localStorage.removeItem('tasks');
      }
    }
  },
  methods: {
    addTask() {
      if (this.task !== '') {
        this.tasks.push(this.task);
        this.saveTasks();
        this.setDefaultTaskValues();
      }
    },
    completeTask(n) {
     this.tasks[n].completed = 1;
     this.saveTasks();
     this.setDefaultTaskValues();
    },
    deleteTask(n) {
      this.tasks.splice(n, 1);
      this.saveTasks();
    },
    saveTasks() {
      const parsed = JSON.stringify(this.tasks);
      localStorage.setItem('tasks', parsed);
    },
    setDefaultTaskValues() {
      this.task = {};
      this.task.content = '';
      this.task.completed = 0;
    }
  },
}
</script>

<style>
#app {
  font-family: 'Pacifico', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #292222;
  margin-top: 40px;
  font-size: 1rem;
}

html {
    background-color: #292222;
}

header {
  color: #eeeeee;
}

footer {
  background-color: #292222;
  color: white;
}

button {
  color: #ffffff;
}

#main-header {
  font-size: 5rem;
  letterSpacing: 2px;
}

#pad {
  background-color: #ffffff;
  border: 4px solid #000000;
}

#tasks {
  font-size: 1.7rem;
}

#task-input {
  letter-spacing: 3px;
  font-size: 1.4rem;
}

.task:hover {
  background-color: #dddddd;
  cursor: pointer;
}

.delete:hover {
  color: #ee0000;
  cursor: pointer;
}

.line {
  text-decoration: line-through;
}

.completed {
  background-color: #009900;
  opacity: 0.5;
}

.completed:hover {
  background-color: #009900;
  opacity: 0.9;
}

.complete:hover {
  color: #0981f9;
  cursor: pointer;
}

</style>



